<template>
    <div class='worklistSign'>
        <div class='topInfo'>
          <div class="title">
            <span class='fundInfo' @click='goback()'>工作台</span>
            <img src='../../assets/images/icon_nav_arrow.png'/>
            <span class='branch'>{{text}}</span>
          </div>
          <div class="searchSign">
              <input type="text" placeholder="输入姓名或手机号" v-model="userStr" class="el-input__inner userInput" :class="vipUser != 3 ? 'redborder' : ''"/>
              <div class="search" @click="searchSign">搜索签到</div>
              <i class="iconfont icon-icon_delet" @click="clearUserstr" v-show="isHide"></i>
              <ul class="searchList" v-if="isShowFlag">
                  <li class="searchItem" v-for="(item,index) in searchList" :key="index" @click="searchClick(item)">{{item.userName || item.email || item.phone}}</li>
              </ul>
              <div class="errorTip" v-if="errorTip">
                <i class="iconfont icon-icon_warning"></i>
                <span>{{errorTip}}</span>
              </div>
            </div>
        </div>
        <div class='content'>
            <div class='context'>{{userType}}</div>
        </div>
        <div class="noUserInfo" v-if="vipUser == 1  && editFlag">
          <p class="tipsText">该用户暂不是会员，填写信息后进行潜客追踪</p>
          <div class="addCard" @click="newEdit()">
            <i class="iconfont icon-btn_icon_add"></i>
            填写潜客信息
          </div>
        </div>
        <!-- 非编辑状态 -->
        <div class="userInfo"  v-if="editFlag && vipUser != 1">
          <div class="userId">
            <span :class="form.personId ? '' : 'yincan'">ID: {{form.personId}}</span>
            <div class="operation">
              <div class="edit" v-if="editFlag" @click="edit()">
                  <i class="iconfont icon-btn_edit"></i>
                  <span>编辑</span>
              </div>
              <div class="cancle" v-if="!editFlag" @click="cancle()">
                取消
              </div> 
              <div class="sub" v-if="!editFlag" @click="submit()">
                提交
              </div>
            </div>
          </div>
          <div class="infoDetail" v-if="editFlag">
            <div class="userHeadImg">
              <img :src="form.headImage" class="headImg"/>
              <div class="username">{{form.name}}
                <i class="iconfont icon-icon_boy" v-if="form.sex2 == '男'"></i>
                <i class="iconfont icon-icon_girl" v-if="form.sex2 == '女'"></i>
              </div>
            </div>
            <ul class="infoList">
              <li class="infoItem">
                <span class="itemName">生日:</span>
                <span class="itemValue">{{form.birthday}}</span>
              </li>
              <li class="infoItem">
                <span class="itemName">手机:</span>
                <span class="itemValue">{{form.phone}}</span>
              </li>
              <li class="infoItem">
                <span class="itemName">证件类型:</span>
                <span class="itemValue">{{form.personalType2}}</span>
              </li>
              <li class="infoItem">
                <span class="itemName">证件号:</span>
                <span class="itemValue">{{form.personalIdNo}}</span>
              </li>
            </ul>
            <ul class="infoList">
              <li class="infoItem">
                <span class="itemName">所属门店:</span>
                <span class="itemValue">{{form.branchOfficeName}}</span>
              </li>
              <li class="infoItem">
                <span class="itemName">分配销售:</span>
                <div class="itemValueList">
                  <!-- <span v-for = "(item, index) in form.distributeSale" :key="index">{{item}}</span> -->
                  <span>{{salespersonName}}</span>
                </div>
              </li>
              <li class="infoItem">
                <span class="itemName">预约教练:</span>
                <div class="itemValueList">
                  <span v-for = "(item, index) in coachList2" :key="index">{{item.coachName}}</span>
                </div>
              </li>
              <li class="infoItem" v-if="vipUser!=3">
                <span class="itemName">跟踪周期:</span>
                <span class="itemValue">{{form.tailPeriod}}</span>
              </li>
            </ul>
            <ul class="infoList">
              <li class="infoItem" v-if="vipUser!=3">
                <span class="itemName">周期内次数:</span>
                <span class="itemValue">{{form.periodTimes}}</span>
              </li>
              <li class="infoItem noflex">
                <span class="itemName">备注:</span>
                <div class="itemValue remarkes">{{form.note}}</div>
              </li>
            </ul>
          </div>
        </div>

        <!-- 编辑状态 -->
        <div class="userInfo editUserInfo" v-if="!editFlag">
          <div class="userId">
            <span :class="form.personId ? '' : 'yincan'">ID: {{form.personId}}</span>
            <div class="operation">
              <div class="edit" v-if="editFlag" @click="edit()">
                  <i class="iconfont icon-btn_edit"></i>
                  <span>编辑</span>
              </div>
              <div class="cancle" v-if="!editFlag" @click="cancle()">
                取消
              </div> 
              <div class="sub" v-if="!editFlag" @click="submit()">
                提交
              </div>
            </div>
          </div>

          <div class="inputInfo" v-if="vipUser!=3">
              <div class="editTop">
                  <img src="../../assets/images/qiankeImg.png" class="headerImg" v-if="!form.headImage"/>
                  <img :src="form.headImage" class='headerImg' v-if="form.headImage"/>
                  <input type="file" name="fileToUpload" id="fileToUpload" @change="fileSelected($event)" accept="image/*">
                  <!-- <img :src="form.headImage" class="headerImg"/> -->
              </div>
              <div class="editBottom">
                <el-form ref="form" :model="form" :rules='rules'>
                  <div class="left">
                    <el-form-item label="姓名：" class='formNew' prop='name'>
                        <el-input v-model="form.name" class='inputNew' placeholder="请填写姓名" maxlength = '15'></el-input>
                    </el-form-item>
                    <el-form-item label="性别：" class='formNew noback' prop='sex'>
                      <el-select v-model="form.sex" placeholder="请选择">
                        <el-option
                          v-for="item in sexOptions"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                          >
                        </el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="生日：" class='formNew birthday' prop='birthday'>
                      <el-date-picker v-model="form.birthday" :default-value="startTime" :date-arrow-control="true"
                        type="date" placeholder="请选择日期" class="inputNew">
                      </el-date-picker>
                        <!-- <el-input v-model="form.birthday" class='inputNew' placeholder="请填写姓名"></el-input> -->
                    </el-form-item>
                    <el-form-item label="手机：" class='formNew' prop='phone'>
                        <el-input v-model="form.phone" class='inputNew' placeholder="请填写手机号" maxlength = '11'></el-input>
                    </el-form-item>
                    <el-form-item label="证件类型：" class='formNew noback nomust'>
                        <el-select v-model="form.personalType" placeholder="请选择">
                          <el-option
                            v-for="item in cardOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </div>
                  <div class="center">
                    <el-form-item label="证件号：" class='formNew nomust'>
                        <el-input v-model="form.personalIdNo" class='inputNew' placeholder="请填写证件号"></el-input>
                    </el-form-item>
                    <el-form-item label="所属门店：" class='formNew noback' prop='branchOfficeId'>
                        <el-select v-model="form.branchOfficeId" placeholder="请选择" @change="changeBranch($event)">
                          <el-option
                            v-for="item in branchOptions"
                            :key="item.value"
                            :label="item.name"
                            :value="item.id"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="预约教练：" class='formNew noback nomust'>
                        <el-select v-model="form.reserveCoachId" placeholder="请选择">
                          <el-option
                            v-for="item in coachList"
                            :key="item.value"
                            :label="item.coachName"
                            :value="item.coachId"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="分配销售：" class='formNew noback nomust'>
                        <el-select v-model="form.salespersonUserId" placeholder="请选择" @change="changeSaleId($event)" @focus="checkBranchId(form.branchOfficeId)">
                          <el-option
                            v-for="item in saleList"
                            :key="item.value"
                            :label="item.name"
                            :value="item.id"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="跟踪周期：" class='formNew nomust errorTips' prop="tailPeriod">
                        <el-input v-model="form.tailPeriod" class='inputNew' placeholder="" maxlength = '5' @blur="checkNumber(form.tailPeriod,1)"></el-input>
                        <div class="error">{{errorTips}}</div>
                    </el-form-item>
                  </div>
                  <div class="right">
                    <el-form-item label="周期内次数：" class='formNew nomust errorTips' prop="periodTimes">
                        <el-input v-model="form.periodTimes" class='inputNew' placeholder="" maxlength = '5' @blur="checkNumber(form.periodTimes,2)"></el-input>
                        <div class="error">{{errorTips2}}</div>
                    </el-form-item>
                    <el-form-item label="备注：" class='formNew nomust formArea'>
                        <el-input
                          type="textarea"
                          :rows="4"
                          placeholder="请输入内容"
                          v-model="form.note"
                          maxlength="200"
                          >
                        </el-input>
                    </el-form-item>
                  </div>
                 </el-form> 
              </div>
          </div>

          <div class="inputInfo" v-if="vipUser==3">
              <div class="editTop">
                  <img src="../../assets/images/qiankeImg.png" class="headerImg" v-if="!form.headImage"/>
                  <img :src="form.headImage" class='headerImg' v-if="form.headImage"/>
                  <input type="file" name="fileToUpload" id="fileToUpload" @change="fileSelected($event)" accept="image/*">
              </div>
              <div class="editBottom">
                <el-form ref="form" :model="form" :rules='rules'>
                  <div class="left">
                    <el-form-item label="姓名：" class='formNew' prop='name'>
                        <el-input v-model="form.name" class='inputNew' placeholder="请填写姓名" maxlength = '15'></el-input>
                    </el-form-item>
                    <el-form-item label="性别：" class='formNew'>
                        <div class="itemValue">{{form.sex2}}</div>
                    </el-form-item>
                    <el-form-item label="生日：" class='formNew birthday'>
                        <div class="itemValue">{{form.birthday}}</div>
                    </el-form-item>
                    <el-form-item label="手机：" class='formNew'>
                        <div class="itemValue">{{form.phone}}</div>
                    </el-form-item>
                  </div>
                  <div class="center">
                    <el-form-item label="证件类型：" class='formNew nomust'>
                        <div class="itemValue">{{form.personalType2}}</div>
                    </el-form-item>
                    <el-form-item label="证件号：" class='formNew nomust'>
                        <div class="itemValue">{{form.personalIdNo}}</div>
                    </el-form-item>
                    <el-form-item label="所属门店：" class='formNew noback' prop='branchOfficeId'>
                        <el-select v-model="form.branchOfficeId" placeholder="请选择" @change="changeBranch($event)">
                          <el-option
                            v-for="item in branchOptions"
                            :key="item.value"
                            :label="item.name"
                            :value="item.id"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="分配销售：" class='formNew noback nomust'>
                        <el-select v-model="form.salespersonUserId" placeholder="请选择" @change="changeSaleId($event)" @focus="checkBranchId(form.branchOfficeId)">
                          <el-option
                            v-for="item in saleList"
                            :key="item.value"
                            :label="item.name"
                            :value="item.id"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                  </div>
                  <div class="right">
                    <el-form-item label="预约教练：" class='formNew noback nomust'>
                        <el-select v-model="form.reserveCoachId" placeholder="请选择">
                          <el-option
                            v-for="item in coachList"
                            :key="item.value"
                            :label="item.coachName"
                            :value="item.coachId"
                            >
                          </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="备注：" class='formNew nomust formArea'>
                        <el-input
                          type="textarea"
                          :rows="4"
                          placeholder="请输入内容"
                          v-model="form.note"
                          maxlength="200"
                          >
                        </el-input>
                    </el-form-item>
                  </div>
                 </el-form> 
              </div>
          </div>

        </div>

        <div class="cardInfo">
          <div class='content' v-if="vipUser == 3">
              <div class='context'>会员卡信息</div>
          </div>
          <div class="addCard" v-if="vipUser == 3" @click="toAddCard">
            <i class="iconfont icon-btn_icon_add"></i>
            添加会员卡
          </div>
          <Table  :tableData="tableData" :tabhide="tabhide" :tableKey="tableKey" ref="Table" :total="total" :isPage = "isPage" :pageSize = "pageSize" :currentPage="currentPage" :isShowSelectionBtn="isShowSelectionBtn" v-on:getQueryParam = "getQueryParam"
       v-on:changeVal="changeParam" v-on:getRowdata="getRowdata" v-if="vipUser == 3"></Table>

          <div class="signBtn" v-if="vipUser != 1">
              <div class="cancle" @click="cancleSign">取消</div>
              <div class="confirm" @click="confirmSign" :class="!editFlag ? 'noclick' : ''">
                <i class="iconfont icon-icon_checkin1"></i>
                  确认签到
              </div>
          </div>
        </div>
    </div>
</template>
<script>
import Table from '../../components/Table'
import Vue from 'vue'
import {workspaceSearch, queryUserInfo, queryPotentialInfo, getBranchName, getbranchcoach, upload, addPotential, editPotential, addUserVip, workspaceEntrance, querypercard, downMedia, querySaleList} from '../../services/service.js'
import {isApiResponseOk, getSessionStorage, timeformer} from '../../utils/com.js'
export default {
  data () {
    var checkContact = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入联系方式'));
      } else {
        let FormatReg = /^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/
        if (!FormatReg.test(value)) {
            callback ('请填写正确的手机号码')
        } else {
          callback()
        }
      }
    };
    return {
      form: {
        personId: '', 
        headImage: '',
        name: '', 
        sex: '',
        sex2: '',
        birthday: '',
        phone: '', 
        personalType: '',
        personalType2: '',
        personalIdNo: '', 
        branchOfficeId: '',
        salespersonUserId: '',
        reserveCoachId: '',
        note: '', 
        tailPeriod: '', 
        periodTimes: ''
      },
      rules: {
        name: [{required: true, message: '姓名不能为空', trigger: 'blur'}],
        sex: [{type: 'string', required: true, message: '性别不能为空', trigger: ['blur', 'change'] }],
        birthday: [
          {required: true, message: '生日不能为空', trigger: ['blur', 'change']}
        ],
        phone: [
          { required: true, validator: checkContact, trigger: 'blur' }
        ],
        branchOfficeId: [
          { type: 'string', required: true, message: '门店不能为空', trigger: ['blur', 'change']}
        ],
        salespersonUserId: [
          { required: true, message: '销售不能为空', trigger: ['blur', 'change']}
        ]
        // tailPeriod:[{ type: 'number', required: true, message: '次数必须是整数', trigger: 'blur'}],
        // periodTimes:[{ type: 'number', required: true, message: '次数必须是整数', trigger: 'blur'}]
      },
      sexOptions: [
        {value: '0',label: '男'},
        {value: '1',label: '女'}
      ],
      cardOptions: [
        {value: 'MainlandIDCard',label: '身份证'},
        {value: 'HongkongIDCard',label: '香港居民证'},
        {value: 'Passport',label: '护照'},
        {value: 'TaiBaoCard',label: '台胞证'},
      ],
      branchOptions: [],
      coachList: [],
      coachList2: [],
      tableData: [
      ],
      tableKey: [
        {name: '会员卡ID', value: 'code', nor: true},
        {name: '会员卡种类', value: 'name', nor: true},
        {name: '类型', value: 'type', nor: true},
        {name: '绑定会员', value: 'personName', nor: true},
        {name: '余额', value: 'balance', balance: true},
        {name: '会员卡状态', value: 'status', nor: true},
        {name: '创建时间', value: 'createTime', nor: true}
      ],
      isShowSelectionBtn: false,
      isPage: false,
      total: 0,
      currentPage: 1,
      pageSize: 5,
      tabhide: true,
      text: '签到',
      errorTip: '',
      userStr:'',
      personId: '',
      isHide: false,
      userType: '',
      editFlag: true,
      vipUser: 1, // 1-新用户，2-潜客，3-会员,
      appointCoachlist: [],
      searchList: [],
      saleList: [],
      isShowFlag: false,
      potentialVipUserId: '',
      salespersonName: '',
      errorTips: '',
      errorTips2: '',
      startTime: ''
    }
  },
  components: {
    Table
  },
  watch: {
    userStr: function () {
      if (this.userStr) {
        this.isHide = true
      } else {
        this.isHide = false
      }
    }
  },
  created () {
    this.initBranch()
    this.getallcoach()
    if (this.$route.query.vipUser == 1) {
      this.initAllData(this.$route.query.str,this.$route.query.personId,this.$route.query.vipUser)
    } else if (this.$route.query.vipUser == 2) {
      this.initAllData(this.$route.query.str,this.$route.query.potentialVipUserId,this.$route.query.vipUser)
    } else {
      this.initAllData(this.$route.query.str,this.$route.query.personId,this.$route.query.vipUser)
    }
    let time = new Date(1990,0,1)
    this.startTime = time.getTime()
  },
  methods: {
    initAllData (str,personId,vipUser) {
      this.userStr = str
      this.personId = personId
      this.vipUser = vipUser
      this.potentialVipUserId = personId
      if (this.vipUser == 1) { // 新用户
        this.errorTip = '该用户暂不是会员'
        this.userType = '潜客基本信息'
        this.form.name = str
        return
      }
      if (this.vipUser == 2) { // 潜客
        this.userType = '潜客基本信息'
        this.errorTip = '该用户暂不是会员'
        this.form.name = str
        this.queryPotentialInfo(this.personId)
      }
      if (this.vipUser == 3) { // 会员
        this.userType = '会员基本信息'
        this.errorTip = ''
        this.queryUserInfo(this.personId)
        this.querypercard(this.personId)
      }
    },
    // 模糊查询
    async workspaceSearch (str) {
      if (!str) {
        return
      }
      const res = await workspaceSearch(str)
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.searchList = res.data
          this.isShowFlag = true
          if (this.searchList.length == 0) {
            this.$router.push({ name: "worklistSign", query: { str: str, personId: '', vipUser: 1} });
            this.initAllData(str,'',1)
          }
        }
      }
    },
    // 搜索列表点击
    searchClick (item) {
      var str = item.userName || item.email || item.phone
      this.isShowFlag = false
      if (item.status == 1) {
        this.initAllData(str,item.personId,item.status)
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, personId: item.personId, vipUser: item.status} });
      } else if (item.status == 2) {
        this.initAllData(str,item.potentialVipUserId,item.status)
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, potentialVipUserId: item.potentialVipUserId, vipUser: item.status} });
      } else {
        this.initAllData(str,item.personId,item.status)
        this.$router.push({ name: "worklistSign", query: { str: item.userName || item.email || item.phone, personId: item.personId, vipUser: item.status} });
      }
    },
    // 初始化门店信息
    async initBranch  () {
      const res = await getBranchName()
      if (isApiResponseOk(res)) {
        if (res.data) {
          this.branchOptions = res.data
        }
      }
    },
    // 获取教练列表
    async getallcoach (branchId) {
      const res = await getbranchcoach({branchId: branchId})
      if (isApiResponseOk(res)) {
        this.coachList = res.data
      }
    },
    // 查询对应健身房销售
    getSaleList (str) {
      let that = this
      querySaleList(str).then(function (res) {
        if (res && res.data) {
          that.saleList = res.data
        } else {
          that.saleList = []
        }
      })
    },
    // 选择门店
    changeBranch (event) {
      // console.log(this.form)
      this.branchId = event
      this.getallcoach(event)
      this.getSaleList(event)
      Vue.set(this.form, 'salespersonUserId', '')
      // this.form.salespersonUserId = ''
    },
    changeSaleId (event) {
      // console.log(event)
      // console.log('进来')
      // this.form.salespersonUserId = event
      // console.log(this.saleList)
    },
    checkNumber (num,n) {
      if (num) {
        // num = parseInt(num)
        var reg = /(^[0-9][0-9]*(.[0-9]+)?)$/
        var patten =  new RegExp(reg)
        // console.log(num.toString() == 'NaN')
        if (!patten.test(num)) {
          if (n==1) {
            this.errorTips = '次数为正整数'
          } else {
            this.errorTips2 = '次数为正整数'
          }
        } else {
          if (n==1) {
            this.errorTips = ''
          } else {
            this.errorTips2 = ''
          }
        }
      } else {
        if (n==1) {
            this.errorTips = ''
        } else {
          this.errorTips2 = ''
        }
      }
    },
    checkBranchId (str) {
      // console.log(this.saleList)
      // if (!str) {
      //   this.saleList = []
      //   this.$message({
      //     showClose: true,
      //     message: '请先选择门店',
      //     type: 'warning'
      //   })
      // }
    },
    // 获取会员信息
    async queryUserInfo (data) {
      const res = await queryUserInfo(data)
      if (isApiResponseOk(res)) {
        if (res.data) {
          let personObj = res.data
          if (personObj.headImag && personObj.headImag.indexOf('http') < 0) {
            const response = await downMedia ({mediaId: personObj.headImag})
            if (response) {
              this.form.headImage = response
            }
          } else {
            this.form.headImage = personObj.headImag
          }
          this.form.personId = personObj.personId
          this.form.name = personObj.userName
          this.form.sex = personObj.personSex.toString()
          if (personObj.personSex == 0) {
            this.form.sex2 = '男'
          } else {
            this.form.sex2 = '女'
          }
          this.form.birthday =  timeformer(personObj.birthday)
          // this.form.birthday = personObj.birthday
          this.form.phone = personObj.userPhone
          this.form.personalType = personObj.personalType
          this.form.personalType2 = this.filterType(personObj.personalType)
          this.form.personalIdNo = personObj.personalIdNo
          this.form.branchOfficeId = personObj.branchOfficeId
          this.form.branchOfficeName = personObj.branchOfficeName
          this.form.salespersonUserId = personObj.salespersonUserId
          this.form.reserveCoachId = personObj.reserveCoachId
          this.form.note = personObj.note
          this.coachList2 = personObj.coachList
          this.salespersonName = personObj.salespersonName
        }
      }
    },
    // 返回上一层
    goback () {
      this.$router.push('worklist')
    },
    // 搜索签到
    searchSign () {
      this.workspaceSearch(this.userStr)
      this.editFlag = true
    },
    // 清空搜索框
    clearUserstr () {
      this.userStr = ''
    },
    // 编辑
    edit () {
      this.editFlag = false
    },
    newEdit () {
      this.form = {}
      this.editFlag = false
    },
    filterType (str) {
      switch (str) {
        case 'MainlandIDCard':
            return '大陆居民身份证'
        case 'HongkongIDCard':
            return '香港居民身份证'
        case 'Passport':
            return '护照'
        case 'TaiBaoCard':
            return '台胞证'
        default:
            return  '大陆居民身份证'
      }
    },
    // 取消
    cancle () {
      this.editFlag = true
    },
    submit () {
      let that = this
      if (this.errorTips || this.errorTips2) {
        return
      }
      this.$refs['form'].validate((valid) => {
        if (valid) {
          if (!that.form.headImage) {
            that.$message({
              showClose: true,
              message: '头像未上传',
              type: 'warning'
            })
            return
          }
          that.form.tailPeriod = parseInt(that.form.tailPeriod)
          that.form.periodTimes = parseInt(that.form.periodTimes)
          let time = new Date(that.form.birthday)
          if (typeof(that.form.birthday) == 'number') {
          } else if (that.form.birthday[4] == '-'){
            let time = new Date(that.form.birthday)
            that.form.birthday = time.getTime()
          } else {
            that.form.birthday = that.form.birthday.getTime()
          }
          if (this.$route.query.vipUser == 1) {
            addPotential(that.form).then(function (res) {
              if (isApiResponseOk(res)) {
                that.$message({
                  showClose: true,
                  message: '添加成功',
                  type: 'success'
                })
                that.editFlag = true
                that.userStr = that.form.name
                that.potentialVipUserId = res.data
                that.$router.push({ name: "worklistSign", query: { str: that.form.name, potentialVipUserId: that.potentialVipUserId, vipUser: 2} });
                that.initAllData(that.form.name,that.potentialVipUserId,2)
                that.queryPotentialInfo(that.potentialVipUserId)
                // self.$router.push({name: 'privateLimit'})
              } else {
                that.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          } else if (this.$route.query.vipUser == 2) {
            editPotential(that.form,this.potentialVipUserId).then(function (res) {
              if (isApiResponseOk(res)) {
                that.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
                })
                that.editFlag = true
                that.queryPotentialInfo(that.potentialVipUserId)
                // self.$router.push({name: 'privateLimit'})
              } else {
                that.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          } else if (this.$route.query.vipUser == 3) {
            let obj = {
              userName: this.form.name,
              headImag: this.form.headImage,
              note: this.form.note,
              personId: this.form.personId,
              salespersonUserId: this.form.salespersonUserId
            }
            addUserVip(obj).then(function (res) {
              if (isApiResponseOk(res)) {
                that.$message({
                  showClose: true,
                  message: '修改成功',
                  type: 'success'
                })
                that.editFlag = true
                that.queryUserInfo(that.personId)
                // self.$router.push({name: 'privateLimit'})
              } else {
                that.$message({
                  showClose: true,
                  message: res.errMsg,
                  type: 'warning'
                })
              }  
            })
          } 
        }
      })
    },
    getQueryParam (queryObj) {
      // console.log('获取参数', queryObj)
      // this.currentList = []
      // this.IndexLsit = []
    },
    changeParam (data) {
      // console.log('获取数据', data)
    },
    getRowdata (data) {
      // console.log('获取的数据', data)
    },
    toAddCard () {
      this.$router.push({ name: "addmembercard"});
    },
    // 取消签到
    cancleSign () {
      this.$router.push('worklist')
    },
    // 确认签到
    confirmSign () {
      if (!this.editFlag) {
        return
      }
      let that = this
      let obj = {}
      if (this.$route.query.vipUser == 3) {
        workspaceEntrance(this.form.personId,this.form.branchOfficeId,3).then(function (res) {
          if (res && res.errorCode == 0) {
            that.$message({
              showClose: true,
              message: '签到成功',
              type: 'success'
            })
          } else {
            that.$message({
              showClose: true,
              message: res.errMsg,
              type: 'warning'
            })
          }
        })
      } else {
        workspaceEntrance(this.potentialVipUserId,this.form.branchOfficeId,2).then(function (res) {
          if (res && res.errorCode == 0) {
            that.$message({
              showClose: true,
              message: '签到成功',
              type: 'success'
            })
          } else {
            that.$message({
              showClose: true,
              message: res.errMsg,
              type: 'warning'
            })
          }
        })
      }
    },
    // 图片上传
    async fileSelected (obj) {
      let formData = new FormData()
      let file = document.getElementById('fileToUpload').files[0]
      if (!file) return
      formData.append('image', file)
      const res = await upload(formData)
      if (isApiResponseOk(res)) {
        this.form.headImage = res.data
        this.$message({
          showClose: true,
          message: '恭喜您,上传成功',
          type: 'success'
        })
        this.userStr += 'a'
        this.userStr = this.userStr.substring(0,this.userStr.length-1)
      } else {
        this.$message({
          message: '上传失败，请重新上传',
          type: 'warning'
        })
      }
    },
    // 查询潜客信息
     async queryPotentialInfo (personId) {
      let that = this
      const res = await queryPotentialInfo(personId)
      if (isApiResponseOk(res)) {
        // console.log(res.data)
        let infoObj = res.data
        that.form.personId = infoObj.personId
        if (infoObj.headImage && infoObj.headImage.indexOf('http') < 0) {
          const response = await downMedia ({mediaId: infoObj.headImage})
          if (response) {
            that.form.headImage = response
          }
        } else {
          that.form.headImage = infoObj.headImage
        }
        that.form.name = infoObj.name
        that.form.sex = infoObj.sex.toString()
        if (infoObj.sex == 0) {
          that.form.sex2 = '男'
        } else {
          that.form.sex2 = '女'
        }
        that.form.birthday = timeformer(infoObj.birthday)
        that.form.phone = infoObj.phone
        that.form.personalType = infoObj.personalType
        that.form.personalType2 = that.filterType(infoObj.personalType)
        that.form.personalIdNo = infoObj.personalIdNo
        that.form.branchOfficeId = infoObj.branchOfficeId
        that.form.branchOfficeName = infoObj.branchOfficeName
        that.coachList2 = []
        that.coachList2.push({coachName: infoObj.reserveCoachName})
        that.form.reserveCoachId = infoObj.reserveCoachId
        that.form.note = infoObj.note
        that.form.tailPeriod = infoObj.tailPeriod
        that.form.periodTimes = infoObj.periodTimes
        that.form.salespersonUserId = infoObj.salespersonUserId
        that.salespersonName = infoObj.salespersonName
      }
      // queryPotentialInfo(personId).then(function (res){
        
      // })
    },
    // 获取会员卡信息
    async querypercard (data) {
      const res = await querypercard (data)
      if (isApiResponseOk(res)) {
        if (res && res.data) {
          for (var i=0; i<res.data.length; i++) {
            res.data[i].personName = this.$route.query.str
          }
          this.tableData = res.data
        }
        
      }
    },
  }
}
</script>
